import React from "react"
import AniLink from "gatsby-plugin-transition-link/AniLink"
import Btn from "./btn"

const Nav = () => (
    <div>

        <AniLink fade to="/live">    <Btn btntext="live" paddingLeft="0" /> </AniLink>
        <AniLink fade to="/work">   <Btn btntext="work" paddingLeft="3px" marginLeft="40px" /> </AniLink>
        <AniLink fade to="/relax">  <Btn btntext="relax" paddingLeft="0" /> </AniLink>
        <AniLink fade to="/play">  <Btn btntext="play" paddingLeft="3px" marginLeft="40px" /> </AniLink>
    </div>

)

export default Nav