import React from 'react'
import AniLink from "gatsby-plugin-transition-link/AniLink"
import PropTypes from 'prop-types'

const Logo = ({ title, color }) => (

    <div>
        <AniLink fade to="/">
            <header style={{
                fontFamily: `Open Sans`,
                fontWeight: `300`,
                fontSize: `54px`,
                textTransform: `uppercase`,
                color: color,
                paddingTop: `30px`

            }}>
                {title}
            </header>
        </AniLink>
    </div>

)

Logo.propTypes = {
    title: PropTypes.string,
}


export default Logo


